import React, { useState, useEffect } from 'react';
import { Button, Spin } from 'antd';
import { DownloadOutlined } from '@ant-design/icons';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Autoplay } from 'swiper/modules';
import 'swiper/css';

const Banner = () => {
    const [bannerImages, setBannerImages] = useState([]);
    const [loading, setLoading] = useState(true);

    // 
    useEffect(() => {
        // 
        const fetchImages = async () => {
            try {
                const imageCount = 6; // 0-56
                const images = Array.from({ length: imageCount }, (_, i) =>
                    `/picture/${i}.jpg` // public/picture
                );
                setBannerImages(images);
            } catch (error) {
                console.error(':', error);
                // 
                setBannerImages(['/picture/0.jpg']);
            } finally {
                setLoading(false);
            }
        };

        fetchImages();
    }, []);

    return (
        <div className="banner-container" id="home" style={{ position: 'relative', height: '70vh' }}>
            {loading ? (
                // 
                <div style={{
                    height: '100%',
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    background: '#000'
                }}>
                    <Spin size="large" tip="..." style={{ color: '#fff' }} />
                </div>
            ) : (
                <>
                    {/*  */}
                    <Swiper
                        modules={[Autoplay]}
                        spaceBetween={0}
                        slidesPerView={1}
                        autoplay={{
                            delay: 5000,
                            disableOnInteraction: false
                        }}
                        loop={true}
                        style={{ height: '100%' }}
                    >
                        {bannerImages.map((imgUrl, index) => (
                            <SwiperSlide key={index}>
                                <div
                                    style={{
                                        backgroundImage: `url(${imgUrl})`,
                                        backgroundPosition: 'center',
                                        backgroundSize: 'cover',
                                        height: '100%'
                                    }}
                                />
                            </SwiperSlide>
                        ))}
                    </Swiper>

                    {/*  */}
                    <div style={{
                        position: 'absolute',
                        top: 0,
                        left: 0,
                        right: 0,
                        bottom: 0,
                        background: 'rgba(0,0,0,0.5)',
                        display: 'flex',
                        flexDirection: 'column',
                        justifyContent: 'center',
                        alignItems: 'center',
                        color: 'white',
                        textAlign: 'center',
                        zIndex: 10
                    }}>
                        <h2 style={{ fontSize: '48px', marginBottom: '16px' }}></h2>
                        <p style={{ fontSize: '24px', marginBottom: '32px' }}> </p>
                        <div style={{ marginBottom: '16px', display: 'flex', flexWrap: 'wrap', justifyContent: 'center' }}>
                            <Button
                                type="primary"
                                size="large"
                                icon={<DownloadOutlined />}
                                style={{ margin: '8px', padding: '0 24px' }}
                                href="https://vip.123pan.cn/1828231620/GamePack/AzurChess%20U24.0721.zip"
                            >
                                Win64
                            </Button>
                            <Button
                                type="primary"
                                size="large"
                                icon={<DownloadOutlined />}
                                style={{ margin: '8px', padding: '0 24px' }}
                                href="https://vip.123pan.cn/1828231620/GamePack/AzurChess%20U24.0721.apk"
                            >
                                Android
                            </Button>
                            <Button
                                type="primary"
                                size="large"
                                style={{ margin: '8px', padding: '0 24px' }}
                                as="a"  // antdasa
                                href="#about"
                            >
                                
                            </Button>
                        </div>
                        <p>U24.0721</p>
                    </div>
                </>
            )}
        </div>
    );
};

export default Banner;