import React, { useState, useEffect } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Pagination, Autoplay } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';

const About = () => {
  const [screenshots, setScreenshots] = useState([]);
  const [loading, setLoading] = useState(true);

  // 
  useEffect(() => {
    const fetchScreenshots = () => {
      try {
        // ss1.pngss5.png
        const screenshotCount = 5; // ss1ss55
        const images = Array.from({ length: screenshotCount }, (_, i) => 
          `/screenshot/ss${i + 1}.png` // public/screenshot
        );
        setScreenshots(images);
      } catch (error) {
        console.error(':', error);
        // 
        setScreenshots(['/screenshot/ss1.png']);
      } finally {
        setLoading(false);
      }
    };

    fetchScreenshots();
  }, []);

  return (
    <div className="aboutus-layout-1" id="about" style={{ padding: '20vh 0' }}>
      <div className="container">
        <div style={{ display: 'flex', flexWrap: 'wrap', alignItems: 'center' }}>
          {/*  */}
          <div style={{ flex: '1', minWidth: '300px', padding: '0 60px' }}>
            <div className="about-content">
              <h2 style={{ fontSize: '32px', marginBottom: '20px', color: '#333' }}></h2>
              <p style={{ fontSize: '16px', lineHeight: '1.8', color: '#666' }}>
                (AzurChess)
                up
                <a href="https://space.bilibili.com/404682135" target="_blank" rel="noopener noreferrer" style={{ color: '#1890ff' }}>
                  @_WAYD
                </a>
                
                <a href="https://space.bilibili.com/35762009" target="_blank" rel="noopener noreferrer" style={{ color: '#1890ff' }}>
                  @SylviaKaslana
                </a>
                
                
                
              </p>
            </div>
          </div>
          
          {/*  */}
          <div style={{ flex: '1', minWidth: '300px', padding: '0 40px', marginTop: '30px' }}>
            {loading ? (
              <div style={{ 
                height: '400px', 
                display: 'flex', 
                justifyContent: 'center', 
                alignItems: 'center', 
                background: '#f5f5f5',
                borderRadius: '12px'  // 
              }}>
              </div>
            ) : (
              <Swiper
                modules={[Navigation, Pagination, Autoplay]}
                spaceBetween={10}
                slidesPerView={1}
                navigation
                pagination={{ clickable: true }}
                autoplay={{ delay: 3000 }}
                loop={true}
                style={{ borderRadius: '12px', overflow: 'hidden' }}  // 
              >
                {screenshots.map((imgUrl, index) => (
                  <SwiperSlide key={index}>
                    <img 
                      src={imgUrl} 
                      alt={`${index + 1}`} 
                      style={{ 
                        width: '100%', 
                        height: '400px',  // 
                        borderRadius: '12px',  // 
                        objectFit: 'cover',  // 
                        background: '#f5f5f5'
                      }} 
                    />
                  </SwiperSlide>
                ))}
              </Swiper>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default About;