// src/pages/CompendiumPage.jsx
import React, { useState, useEffect } from 'react';
import { Card, Row, Col, Spin, Typography, Tag, message, Layout } from 'antd';
import axios from 'axios';
import Header from '../components/Header';
import Footer from '../components/Footer';
const { Content } = Layout;
const { Title, Text } = Typography;
const CompendiumPage = () => {
    const [characters, setCharacters] = useState([]);
    const [nameMap, setNameMap] = useState({});
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    // 
    const typeMap = {
        0: '',
        1: '',
        2: '',
        3: '',
        4: '',
        5: '',
        6: '',
    };
    const weaponTypeMap = {
        0: { color: "#a50000ff", name: "" },
        1: { color: "#004799ff", name: "" },
        2: { color: "#beab00ff", name: "" },
        3: { color: "#009b53ff", name: "" },
        4: { color: "#94007bff", name: "" }
    };
    useEffect(() => {
        const loadData = async () => {
            try {
                setLoading(true);
                const [shipsResponse, nameMapResponse] = await Promise.all([
                    axios.get('/Ships.json'),
                    axios.get('/NameMap.json')
                ]);

                setCharacters(shipsResponse.data);
                setNameMap(nameMapResponse.data);
                setError(null);
            } catch (err) {
                console.error(':', err);
                setError('');
                message.error('');
            } finally {
                setLoading(false);
            }
        };
        loadData();
    }, []);
    if (loading) {
        return (
            <Layout style={{ minHeight: '100vh' }}>
                <Header />
                <Content style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', flex: 1 }}>
                    <Spin size="large" tip="..." />
                </Content>
                <Footer />
            </Layout>
        );
    }
    if (error) {
        return (
            <Layout style={{ minHeight: '100vh' }}>
                <Header />
                <Content style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', flex: 1 }}>
                    <Text type="danger">{error}</Text>
                </Content>
                <Footer />
            </Layout>
        );
    }
    return (
        <Layout style={{ minHeight: '100vh' }}>
            <Header />
            <Content>
                <div id="compendium" style={{ padding: '60px 60px' }}>
                    <div className="container">
                        <Title level={2} style={{ textAlign: 'center', marginBottom: '40px' }}>
                            
                        </Title>

                        {characters.length === 0 ? (
                            <div style={{ textAlign: 'center', padding: '50px' }}>
                                <Text></Text>
                            </div>
                        ) : (
                            <Row
                                gutter={[16, 16]}  // 16px16px
                                style={{
                                    display: 'flex',
                                    flexWrap: 'wrap',  // 
                                    justifyContent: 'center'  // 
                                }}
                            >
                                {characters.map((char) => (
                                    // lg/md flex: 1 
                                    <Col
                                        key={char.Name}
                                        style={{
                                            flex: '1 1 auto',  // 
                                            minWidth: 192,     // 
                                            maxWidth: 220,     // 
                                            padding: '0 8px'   // gutter
                                        }}
                                    >
                                        <Card
                                            hoverable
                                            style={{
                                                width: '100%',    // 
                                                maxWidth: 192,    // 
                                                margin: '0 auto', // 
                                                overflow: 'hidden'
                                            }}
                                            cover={
                                                <div style={{
                                                    height: 256,
                                                    width: '100%',
                                                    display: 'flex',
                                                    alignItems: 'center',
                                                    justifyContent: 'center',
                                                    overflow: 'hidden'
                                                }}>
                                                    <img
                                                        alt={nameMap[char.Name] || char.Name}
                                                        src={`/CardImage/${char.Name}.png`}
                                                        style={{
                                                            width: '100%',
                                                            height: '100%',
                                                            objectFit: 'cover'
                                                        }}
                                                        onError={(e) => { e.target.src = '/CardImage/default.png'; }}
                                                    />
                                                </div>
                                            }
                                        >
                                            {/*  */}
                                            <Card.Meta
                                                title={
                                                    <div style={{
                                                        whiteSpace: 'nowrap',
                                                        overflow: 'hidden',
                                                        marginBottom: 8,
                                                    }}>
                                                        <Text strong style={{ fontSize: 16 }}>
                                                            {nameMap[char.Name] || char.Name}
                                                        </Text>
                                                    </div>
                                                }
                                            />
                                            <Tag style={{ fontSize: 12 }}>
                                                {typeMap[char.Type] || ` ${char.Type}`}
                                            </Tag>
                                            <Tag style={{ fontSize: 12 }} color={weaponTypeMap[char.WeaponType].color || "white"}>
                                                {weaponTypeMap[char.WeaponType].name || ` ${char.WeaponType}`}
                                            </Tag>
                                            <div style={{ marginTop: 8, fontSize: 14 }}>
                                                <div style={{ marginBottom: 4, whiteSpace: 'nowrap' }}>
                                                    <Text type="secondary">: </Text>
                                                    <Text>{char.MaxHP}</Text>
                                                </div>
                                                <div style={{ marginBottom: 4, whiteSpace: 'nowrap' }}>
                                                    <Text type="secondary">: </Text>
                                                    <Text>{char.MinDamage}-{char.MaxDamage}</Text>
                                                </div>
                                                <div style={{ marginBottom: 4, whiteSpace: 'nowrap' }}>
                                                    <Text type="secondary">: </Text>
                                                    <Text>{char.AttackRadius}</Text>
                                                </div>
                                                <div style={{ whiteSpace: 'nowrap' }}>
                                                    <Text type="secondary">: </Text>
                                                    <Text>{char.DeployCost}</Text>
                                                </div>
                                            </div>
                                        </Card>
                                    </Col>
                                ))}
                            </Row>
                        )}
                    </div>
                </div>
            </Content>
            <Footer />
        </Layout>
    );
};
export default CompendiumPage;